.pagintion {
  display: flex;
  margin-left: auto;
  margin-right: auto;
  width: 280px;
  height: 40px;

  overflow: hidden;

  &__btn {
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;

    font-family: inherit;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.3333;
    border: none;
    border-radius: 5px;
    overflow: hidden;
    cursor: pointer;
    z-index: 2;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

    background-color: var(--bgc-btn-pagination);

    &:hover {
      // border-radius: 5px;
      background-color: var(--accent-color);
    }
  }
}

.pagination-list {
  width: 200px;
  display: flex;

  &__btn {
    padding: 0;
    width: 40px;
    height: 40px;

    font-family: inherit;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.3333;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    background-color: var(--main-background-color);
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover {
      background-color: var(--accent-color);
    }

    &--active {
      background-color: var(--accent-color);
    }
  }
}
